import React, { Component } from "react";
import Card from "./Card";
import {robots} from "./robots";
import SearchBox from "./Searchbox";
import Scroll from "./Scroll"

class App extends Component {    
   constructor() {
       super()
       this.state = {
           robots: robots,
           search: ""
       }
   }
   findFriends = (event) => {
    this.setState({search: event.target.value})
   }
   componentDidMount() {
       fetch('https://jsonplaceholder.typicode.com/users').then( response => {
           return response.json();
       }).then( users => {
        this.setState({robots:users})

       });
   }
   render() {
       const foundFriend = this.state.robots.filter(robots => {
           return robots.name.toLowerCase().includes(this.state.search.toLowerCase());
       })
    return (<div className="card-container">
        <div className="roboName">
            <h1>RoboFriends</h1>
            <SearchBox searchRobot={this.findFriends}/>
        </div>
        <Scroll>{foundFriend.map(robot => {
        return <Card  num={robot.id} name={robot.name} email={robot.email}/>;})}
        </Scroll>
    </div>);
        }
}

export default App;